.header {
  padding-top: 30px;
  padding-bottom: 30px;
  position: relative;

  &-bg {
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, 0);
    z-index: -1;
  }

  &-content {
    padding-left: 130px;
    padding-right: 130px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-right {
      display: flex;
      align-items: center;
    }

    &-logo {
      line-height: 0;
    }
  }

  &-location {
    padding-left: 43px;
    position: relative;
    display: block;
    // width: 36px;
    // height: 36px;

    &-img {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
      width: 36px;
      height: 36px;
      border: 1px solid #d8e5f1;
      border-radius: 50%;

      img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &-phone {
    padding-left: 43px;
    position: relative;
    margin-right: 11px;
    display: block;
    // width: 36px;
    // height: 36px;

    &-img {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(0, -50%);
      width: 36px;
      height: 36px;
      border: 1px solid #d8e5f1;
      border-radius: 50%;

      img {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &-links {
    display: flex;
    align-items: center;
    margin-right: 55px;

    a {
      line-height: 0;
      margin-right: 8px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &-lang {
    text-transform: uppercase;

    a.active {
      color: #093888;
      font-weight: 600;
    }

    a {
      transition: font-weight .2s ease;

      &:hover {
        font-weight: 600;
      }
    }
  }

  @media(max-width: 1680.99px) {
    &-content {
      padding-left: 100px;
      padding-right: 100px;
    }
  }

  @media(max-width: 1440.99px) {
    &-content {
      padding-left: 80px;
      padding-right: 80px;
    }
  }

  @media(max-width: 1366.99px) {
    padding-top: 20px;
    padding-bottom: 20px;

    &-content {
      padding-left: 35px;
      padding-right: 35px;
    }
  }

  @media(max-width: 1024.99px) {
    &-content {
      justify-content: flex-start;
      padding-left: 0;
      padding-right: 0;

      &-logo {
        order: -1;
        max-width: calc(100% - 124px);
        flex: 0 0 calc(100% - 124px);

        img {
          max-width: 138px;
        }
      }
    }

    &-location {
      position: relative;

      &-name {
        display: none;
        position: absolute;
        // left: 50%;
        // transform: translate(-50%, 0);
        left: -64px;
        top: 30px;
        font-size: 12px;
        width: 164px;
        padding: 10px;
        background: #FFFFFF;
        border-radius: 8px;
        border: 1px solid #EAF3FB;
        z-index: 1;

        &.active {
          display: block;
        }

        &::before, &::after {
          content: '';
          position: absolute;
          left: 50%;
          transform: translate(-50%, 0);
          border: 10px solid transparent;
          border-bottom: 10px solid #EAF3FB;
          top: -20px;
        }

        &::after {
          border-bottom: 10px solid #fff;
          top: -19px;
          left: 50%;
          transform: translate(-50%, 0);
         }
      }
    }

    &-phone {
      margin-right: 0;
      position: relative;

      &-number {
        display: none;
        position: absolute;
        // left: 50%;
        // transform: translate(-50%, 0);
        left: -48px;
        top: 30px;
        font-size: 12px;
        width: 130px;
        padding: 10px;
        background: #FFFFFF;
        border-radius: 8px;
        border: 1px solid #EAF3FB;
        z-index: 1;

        &::before, &::after {
          content: '';
          position: absolute;
          left: 50%;
          transform: translate(-50%, 0);
          border: 10px solid transparent;
          border-bottom: 10px solid #EAF3FB;
          top: -20px;
        }

        &::after {
          border-bottom: 10px solid #fff;
          top: -19px;
          left: 50%;
          transform: translate(-50%, 0);
         }


        &.active {
          display: block;
        }
      }
    }

    &-links {
      display: none;
    }

    &-lang {
      a {
        display: block;
        font-size: 14px;
        width: 36px;
        height: 36px;
        border: 1px solid #d8e5f1;
        border-radius: 50%;
        line-height: 34px;
        text-align: center;
        font-weight: 600;

        &.active {
          display: none;
        }
      }
    }

    &-bg {
      display: none;
    }
  }
}